/* 필터 박스 */
.filter-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 20px;
    background-color: #FAFAFA;
    border: 1px solid #FAFAFA;
    border-radius: 8px;
    margin-bottom: 20px;
}
.group-create-container1 {
  float: right; /* 우측 정렬 */
  border: 2px solid #000; /* 둘레를 검정색으로 */
  background-color: #000; /* 내부를 검정색으로 */
  color: #FAFAFA; /* 글자색을 흰색으로 변경 */
  padding: 10px; /* 박스 안쪽 여백 */
  width: 200px; /* 박스 너비 설정 */
  text-align: center; /* 텍스트 중앙 정렬 */
  border-radius: 3px; /* 박스 모서리를 둥글게 (선택사항) */
}

.toggle-container {
    display: flex;
    gap: 7px;
}

.toggle-button {
    width: 66px;
    height: 45px;
    text-align: center;
    line-height: 45px;
    border-radius: 22.5px;
    background-color: #f0f0f0;
    color: black;
    cursor: pointer;
    border: 1px solid #ddd;
}

.create-group-button {
  text-decoration: none; /* 밑줄 제거 */
  color: #FAFAFA; /* 텍스트 색상 설정 */

}

.toggle-button.active {
    background-color: black;
    color: white;
}

.search-bar {
  flex-grow: 1; /* 검색바가 가능한 공간을 더 차지 */
  height: 45px;
  font-size: 16px;
  border: 1px solid #FAFAFA;
  border-radius: 8px;
  padding: 10px;
  box-sizing: border-box;
}

.search-button, .sort-toggle {
  width: 120px; /* 정렬 버튼과 검색 버튼 크기 고정 */
  height: 45px;
  color: black;
  text-align: center;
  line-height: 45px;
  border-radius: 8px;
  cursor: pointer;
  border: 1px solid #FAFAFA;
}

/* 정렬 토글 옵션 */
.sort-options {
    position: absolute;
    top: 45px;
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);

    z-index: 10;
}

.sort-options div {
    padding: 8px;
    cursor: pointer;
}

.sort-options div:hover {
    background-color: #f0f0f0;
}

/* 그룹 리스트 */
.group-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-around; /* 한 줄에 4개씩 배치 */
  }
  
  /* 그룹 카드 */
  .group-card {
    width: 300px; /* 카드의 너비 */
    padding: 20px;
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  /* 그룹 이미지 컨테이너 */
  .group-image-container {
    width: 100%;
    height: 180px;
    margin-bottom: 10px;
  }
  
  /* 그룹 이미지 */
  .group-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 8px;
  }
  
  /* 그룹 상세 정보 */
  .group-details {
    text-align: left;
  }
  
  /* 그룹 메타 정보 */
  .group-meta {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  
  /* 그룹 이름 */
  .group-name {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  /* 그룹 소개 */
  .group-description {
    font-size: 14px;
    color: #777;
    margin-bottom: 10px;
  }
  
  /* 그룹 통계 */
  .group-stats {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    color: #555;
  }

  .group-title{
    text-align: center;
  }
  
/* 더보기 버튼 */
.load-more-box {
    text-align: center;
    margin-top: 30px;
}

.load-more-button {
    width: 100%;
    height: 50px;
    background-color: #FAFAFA;

    font-size: 16px;
    cursor: pointer;
    border: none;
}
